<template>
	<!-- 创建任务 -->
	<view class="create-body">
		<view style="padding-top: 22rpx;">
			<uni-list>
				<uni-list-item v-if="disabled">
					<view slot="header" class="form-title">
						<text class="required-s">*</text>
						任务编号
					</view>
					<view slot="footer"><input placeholder="请输入" name="input" :value="form.no" disabled /></view>
				</uni-list-item>
				<uni-list-item v-if="disabled">
					<view slot="header" class="form-title">
						<text class="required-s">*</text>
						创建时间
					</view>
					<view slot="footer"><input placeholder="请输入" name="input" :value="form.createTime" disabled /></view>
				</uni-list-item>
				<uni-list-item>
					<view slot="header" class="form-title">
						<text class="required-s">*</text>
						任务名称
					</view>
					<view slot="footer">
						<input placeholder="请输入" name="input" :value="form.name" @input="inputName" :disabled="disabled" />
						<view class="error-style" v-if="errMsg.name != ''">{{ errMsg.name }}</view>
					</view>
				</uni-list-item>
				<uni-list-item>
					<view slot="header" class="form-title">
						<text class="required-s">*</text>
						任务类型
					</view>
					<view slot="footer">
						<picker :value="form.type" @change="changeTaskType" :range="taskTypeOptions" range-key="name" :disabled="disabled">
							<text class="cuIcon-right right-icon"></text>
							<input placeholder="请选择" name="input" :value="form.type" disabled style="float:right" />
						</picker>
						<view class="error-style" v-if="errMsg.type != ''">{{ errMsg.type }}</view>
					</view>
				</uni-list-item>
				<uni-list-item>
					<view slot="header" class="form-title">
						<text class="required-s">*</text>
						巡检内容
					</view>
					<view slot="footer">
						<textarea @input="inputContent" :value="form.content" placeholder="请输入" :disabled="disabled" style="width:400rpx"></textarea>
						<view class="error-style" v-if="errMsg.content != ''">{{ errMsg.content }}</view>
					</view>
				</uni-list-item>
				<uni-list-item>
					<view slot="header" class="form-title">
						<text class="required-s">*</text>
						巡检片区
					</view>
					<view slot="footer">
						<text class="cuIcon-right right-icon"></text>
						<input placeholder="请选择" name="input" :value="form.area" disabled style="float:right" @click="showMap" />
						<view class="error-style" v-if="errMsg.area != ''">{{ errMsg.area }}</view>
					</view>
				</uni-list-item>
				<uni-list-item>
					<view slot="header" class="form-title">
						<text class="required-s">*</text>
						网格警员
					</view>
					<view slot="footer">
						<text class="cuIcon-right right-icon"></text>
						<input placeholder="请选择" name="input" :value="form.police" disabled style="float:right" @click="choosePolices" />
						<view class="error-style" v-if="errMsg.police != ''">{{ errMsg.police }}</view>
					</view>
				</uni-list-item>
				<uni-list-item>
					<view slot="header" class="form-title">
						<text class="required-s">*</text>
						预计完成时间
					</view>
					<view slot="footer">
						<picker mode="date" :value="form.preCompleteTime" @change="changeTime" :disabled="disabled">
							<text class="cuIcon-right right-icon"></text>
							<input placeholder="请选择" name="input" :value="form.preCompleteTime" disabled style="float:right" />
						</picker>
						<view class="error-style" v-if="errMsg.preCompleteTime != ''">{{ errMsg.preCompleteTime }}</view>
					</view>
				</uni-list-item>
			</uni-list>
		</view>
		<view class="flex flex-direction" style="padding:30rpx"><button class="cu-btn bg-blue lg" @click="dispatch">派 发</button></view>
	</view>
</template>

<script>
import { taskAPI } from '@/api/index.js';
export default {
	data() {
		return {
			roleId: uni.getStorageSync('roleId'),
			form: {
				no: '',
				createTime: '',
				name: '',
				type: '',
				typeCode: '',
				content: '',
				area: '',
				areaCode: '',
				police: '',
				policeCode: '',
				preCompleteTime: ''
			},
			areaOptions: [],
			policeOpitons: [],
			taskTypeOptions: [],
			errMsg: {
				name: '',
				type: '',
				content: '',
				area: '',
				police: '',
				preCompleteTime: ''
			},
			id: '',
			disabled: false
		};
	},
	onLoad(options) {
		this.getMidGroupArea();
		this.getTaskTypeOptions();
		this.id = options.id;
		if (this.id) {
			setTimeout(() => {
				this.getTaskDetail(this.id);
			}, 500);
			this.disabled = true;
		}
	},
	onShow() {
		let area = uni.getStorageSync('checkedArea');
		let policeCode = uni.getStorageSync('checkedPoliceId');
		let police = uni.getStorageSync('checkedPoliceName');
		if (area) {
			this.form.area = area;
			this.errMsg.area = '';
		}
		if (police) {
			this.form.policeCode = policeCode;
			this.form.police = police;
			this.errMsg.police = '';
		}
	},
	methods: {
		getMidGroupArea() {
			//查询中队下所有片区
			taskAPI.getMidGroupArea().then(res => {
				this.areaOptions = res.data.map(item => {
					return {
						name: item.areaName,
						value: item.id
					};
				});
			});
		},
		getTaskTypeOptions() {
			//获取任务类型
			taskAPI.getSysDict({ type: 'task_type' }).then(res => {
				if (res.code == 200) {
					this.taskTypeOptions = res.data.map(item => {
						return {
							name: item.label,
							value: item.value
						};
					});
				}
			});
		},
		getTaskDetail(id) {
			taskAPI.getTaskDetail({ id }).then(res => {
				if (res.code == 200) {
					this.form.no = res.data.id;
					this.form.createTime = res.data.createdTime;
					this.form.name = res.data.taskName;
					this.form.typeCode = res.data.taskType;
					this.form.type = this.taskTypeOptions.find(item => item.value == res.data.taskType).name;
					this.form.content = res.data.content;
					this.form.preCompleteTime = res.data.preCompleteDate;
				}
			});
		},
		showMap() {
			//选择片区
			uni.navigateTo({
				url: '../patrolTask/map'
			});
		},
		choosePolices() {
			//选择网格警员
			if (this.form.area == '') {
				uni.showToast({
					icon: 'none',
					title: '请先选择片区'
				});
				return;
			}
			uni.navigateTo({
				url: '../patrolTask/choosePolice'
			});
		},
		changeTaskType(event) {
			this.form.typeCode = this.taskTypeOptions[event.detail.value].value;
			this.form.type = this.taskTypeOptions[event.detail.value].name;
			this.errMsg.type = '';
		},
		changeTime(event) {
			this.form.preCompleteTime = event.detail.value;
			this.errMsg.preCompleteTime = '';
		},
		inputName(event) {
			this.form.name = event.detail.value;
			this.errMsg.name = '';
		},
		inputContent(event) {
			this.form.content = event.detail.value;
			this.errMsg.content = '';
		},
		checkForm() {
			let flag = true;
			if (this.form.area == '') {
				this.errMsg.area = '请选择巡检片区';
				flag = false;
			}
			if (this.form.police == '') {
				this.errMsg.police = '请选择网格警员';
				flag = false;
			}
			return flag;
		},
		dispatch() {
			let flag = this.checkForm();
			if (flag) {
				uni.showModal({
					content: '是否确认派发巡检任务单',
					success: rs => {
						console.log(rs);
						if (rs.confirm) {
							//点击了确定按钮
							//中队派发巡检任务
							let param = {
								id: this.form.no,
								taskName: this.form.name,
								taskType: this.form.typeCode,
								content: this.form.content,
								preCompleteDate: this.form.preCompleteTime,
								checkArea: this.form.area,
								gridPolice: this.form.policeCode
							};
							console.log(param)
							taskAPI.createTask(param).then(res => {
								if (res.code == 200) {
									uni.showToast({
										icon: 'none',
										title: '派发成功',
										success: () => {
											uni.navigateTo({
												url: '../patrolTask/index'
											});
										}
									});
								}
							});
						}
					}
				});
			}
		}
	}
};
</script>

<style scoped>
input,
textarea {
	text-align: right;
}
</style>
